.cfa__minimal {
  padding: 17px 25px;
  position: relative;
  z-index: 1; }
  .cfa__minimal:before {
    content: '';
    position: absolute;
    width: 6px;
    height: 36px;
    top: 50%;
    transform: translateY(-50%);
    left: -3px;
    background: var(--secondary);
    border-radius: 50px; }
  @media (min-width: 992px) {
    .cfa__minimal p {
      margin-left: 30px;
      margin-right: 30px; } }
  @media (min-width: 992px) {
